import React, { Component } from 'react'
import CustomBreadcrumb from '@/components/CustomBreadcrumb'
import {
    Alert,
    Layout,
    Row,
    Col,
    Divider,
    Form,
    Button,
    Icon,
    Input,
    InputNumber,
    Checkbox,
    Tooltip,
    Cascader,
    Select,
    DatePicker,
    Radio,
    Rate,
    Switch,
    Slider,
    AutoComplete,
    message
} from 'antd'
import '@/style/view-style/form.scss'

const { Option } = Select
const AutoCompleteOption = AutoComplete.Option

const residences = [
    {
        value: 'sichuan',
        label: '',
        children: [
            {
                value: 'chengdu',
                label: '',
                children: [
                    {
                        value: 'gaoxin',
                        label: ''
                    }
                ]
            }
        ]
    },
    {
        value: 'gansu',
        label: '',
        children: [
            {
                value: 'lanzhou',
                label: '',
                children: [
                    {
                        value: 'anning',
                        label: ''
                    }
                ]
            }
        ]
    }
]

class FromView extends Component {
    state = {
        confirmDirty: false,
        autoCompleteResult: [],
        visible: true
    }

    handleClose = () => {
        this.setState({ visible: false })
    }

    handleSubmit = e => {
        e.preventDefault()
        this.props.form.validateFieldsAndScroll((err, fieldsValue) => {
            if (err) return
            const values = {
                ...fieldsValue,
                'date-picker': fieldsValue['date-picker'] ? fieldsValue['date-picker'].format('YYYY-MM-DD') : ''
            }
            console.log('' + values)
            message.info(',!')
        })
    }

    handleConfirmBlur = e => {
        const { value } = e.target
        this.setState({ confirmDirty: this.state.confirmDirty || !!value })
    }

    compareToFirstPassword = (rule, value, callback) => {
        const { form } = this.props
        if (value && value !== form.getFieldValue('password')) {
            callback('!')
        } else {
            callback()
        }
    }

    validateToNextPassword = (rule, value, callback) => {
        const { form } = this.props
        if (value && this.state.confirmDirty) {
            form.validateFields(['confirm'], { force: true })
        }
        callback()
    }

    handleWebsiteChange = value => {
        let autoCompleteResult
        if (!value) {
            autoCompleteResult = []
        } else {
            autoCompleteResult = ['@google.com', '@163.com', '@qq.com'].map(domain => `${value}${domain}`)
        }
        this.setState({ autoCompleteResult })
    }

    render() {
        const { getFieldDecorator, getFieldValue } = this.props.form

        const formItemLayout = {
            labelCol: {
                xs: { span: 16 },
                sm: { span: 6 }
            },
            wrapperCol: {
                xs: { span: 16 },
                sm: { span: 10 }
            }
        }
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 16,
                    offset: 0
                },
                sm: {
                    span: 10,
                    offset: 6
                }
            }
        }
        const prefixSelector = getFieldDecorator('prefix', {
            initialValue: '86'
        })(
            <Select style={{ width: 70 }}>
                <Option value='86'>+86</Option>
                <Option value='87'>+87</Option>
            </Select>
        )

        const websiteOptions = this.state.autoCompleteResult.map(website => (
            <AutoCompleteOption key={website}>{website}</AutoCompleteOption>
        ))

        return (
            <Layout className='animated fadeIn'>
                <div>
                    <CustomBreadcrumb arr={['', '']}></CustomBreadcrumb>
                </div>
                <div className='base-style'>
                    <h3></h3>
                    <Divider></Divider>
                    <p></p>
                    <p></p>
                </div>

                <Row>
                    <Col>
                        <div className='base-style'>
                            <div>
                                {this.state.visible ? (
                                    <Alert
                                        message='!'
                                        type='warning'
                                        closable
                                        banner
                                        afterClose={this.handleClose}
                                    />
                                ) : null}
                            </div>
                            <Divider orientation='left'></Divider>
                            <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                                <Form.Item
                                    label={
                                        <span>
                                            &nbsp;
                                            <Tooltip title='!'>
                                                <Icon type='question-circle-o' />
                                            </Tooltip>
                                        </span>
                                    }>
                                    {getFieldDecorator('username', {
                                        rules: [{ required: true, message: '' }]
                                    })(<Input placeholder='' />)}
                                </Form.Item>
                                <Form.Item label=''>
                                    {getFieldDecorator('sex', {
                                        rules: [{ required: true, message: '' }]
                                    })(
                                        <Radio.Group>
                                            <Radio value='man'></Radio>
                                            <Radio value='women'></Radio>
                                            <Radio value='unknow'></Radio>
                                        </Radio.Group>
                                    )}
                                </Form.Item>
                                <Form.Item label=''>
                                    {getFieldDecorator('hobby', {
                                        rules: [{ required: true, message: '' }],
                                        initialValue: ['A', 'B']
                                    })(
                                        <Checkbox.Group style={{ width: '100%' }}>
                                            <Row>
                                                <Col span={8}>
                                                    <Checkbox value='A'>A</Checkbox>
                                                </Col>
                                                <Col span={8}>
                                                    <Checkbox disabled value='B'>
                                                        B
                                                    </Checkbox>
                                                </Col>
                                                <Col span={8}>
                                                    <Checkbox value='C'>C</Checkbox>
                                                </Col>
                                            </Row>
                                        </Checkbox.Group>
                                    )}
                                </Form.Item>
                                <Form.Item label=''>
                                    {getFieldDecorator('age', {
                                        rules: [{ required: true, message: '' }]
                                    })(<InputNumber placeholder='' style={{ width: '100%' }} />)}
                                </Form.Item>
                                <Form.Item label=''>
                                    {getFieldDecorator('date-picker', {
                                        rules: [{ type: 'object', required: true, message: '' }]
                                    })(<DatePicker style={{ width: '100%' }} placeholder='' />)}
                                </Form.Item>

                                <Form.Item label=''>
                                    {getFieldDecorator('email', {
                                        rules: [
                                            {
                                                type: 'email',
                                                message: '!'
                                            },
                                            {
                                                required: true,
                                                message: ''
                                            }
                                        ]
                                    })(
                                        <AutoComplete
                                            dataSource={websiteOptions}
                                            onChange={this.handleWebsiteChange}
                                            placeholder=''>
                                            <Input />
                                        </AutoComplete>
                                    )}
                                </Form.Item>
                                <Form.Item label='' hasFeedback>
                                    {getFieldDecorator('password', {
                                        rules: [
                                            {
                                                required: true,
                                                message: '!'
                                            },
                                            {
                                                validator: this.validateToNextPassword
                                            }
                                        ]
                                    })(<Input.Password placeholder='' />)}
                                </Form.Item>
                                <Form.Item label='' hasFeedback>
                                    {getFieldDecorator('confirm', {
                                        rules: [
                                            {
                                                required: true,
                                                message: '!'
                                            },
                                            {
                                                validator: this.compareToFirstPassword
                                            }
                                        ]
                                    })(<Input.Password onBlur={this.handleConfirmBlur} placeholder='' />)}
                                </Form.Item>
                                <Form.Item label=''>
                                    {getFieldDecorator('adress', {
                                        initialValue: ['sichuan', 'chengdu', 'gaoxin'],
                                        rules: [{ type: 'array', required: true, message: '!' }]
                                    })(<Cascader options={residences} placeholder='' />)}
                                </Form.Item>
                                <Form.Item label='' extra='.'>
                                    {getFieldDecorator('phone', {
                                        rules: [{ required: true, message: '!' }]
                                    })(<Input addonBefore={prefixSelector} />)}
                                </Form.Item>
                                <Form.Item label='' extra='.'>
                                    {getFieldDecorator('rate', {
                                        initialValue: 5
                                    })(<Rate disabled allowHalf />)}
                                </Form.Item>
                                <Form.Item label='switch'>
                                    {getFieldDecorator('switch', {
                                        valuePropName: 'checked',
                                        initialValue: true
                                    })(<Switch />)}
                                </Form.Item>
                                <Form.Item label='slider'>
                                    {getFieldDecorator('slider', {
                                        initialValue: 30
                                    })(<Slider disabled={getFieldValue('switch') ? false : true} />)}
                                </Form.Item>
                                <Form.Item {...tailFormItemLayout}>
                                    {getFieldDecorator('agreement', {
                                        valuePropName: 'checked'
                                    })(
                                        <Checkbox>
                                             <a href='https://github.com/ltadpoles'></a>
                                        </Checkbox>
                                    )}
                                </Form.Item>
                                <Form.Item {...tailFormItemLayout}>
                                    <Button
                                        type='primary'
                                        htmlType='submit'
                                        disabled={getFieldValue('agreement') ? false : true}>
                                        
                                    </Button>
                                </Form.Item>
                            </Form>
                        </div>
                    </Col>
                </Row>
            </Layout>
        )
    }
}

const WrappedNormalLoginForm = Form.create({ name: 'normal_login' })(FromView)

export default WrappedNormalLoginForm
